<template>
    <v-container>

        <v-row>
            <v-col cols="12">
                <h2>Tus idiomas</h2>
            </v-col>
        </v-row>

        <v-col cols="12">
            <v-card>
                <v-progress-linear
                        value="50"
                        height="10"
                        color="blue-grey"
                        reactive
                        dark
                >
                </v-progress-linear>

                <v-container>
                    <h4>Idiomas maternos</h4>
                    <v-combobox
                            v-model="chips_known_languages"
                            :items="items_known_languages"
                            chips
                            clearable
                            label="¿Qué idiomas hablas con soltura?"
                            multiple
                            solo
                    >
                        <template v-slot:item="{ index, item }">
                            <v-chip
                                    color="white"
                                    block
                            >
                                <v-avatar left>
                                    <v-img :src="createPathToFlagImage(item.id)"></v-img>
                                </v-avatar>
                                <strong>{{ item.text }}</strong>&nbsp;
                            </v-chip>
                        </template>
                        <!--<template v-slot:item="{ index, item }">
                            <v-chip
                                    block
                            >
                                <v-avatar left>
                                    <v-img :src = "createPathToFlagImage(item.id)"></v-img>
                                </v-avatar>
                                <strong>{{ item.text }}</strong>&nbsp;
                            </v-chip>
                        </template>-->

                        <template v-slot:selection="{ attrs, item, select, selected }">
                            <v-chip
                                    v-bind="attrs"
                                    :input-value="selected"
                                    close
                                    @click="select"
                                    @click:close="remove_known_language(item)"
                            >
                                <v-avatar left>
                                    <v-img :src = "createPathToFlagImage(item.id)"></v-img>
                                </v-avatar>
                                <strong>{{ item.text }}</strong>&nbsp;
                            </v-chip>
                        </template>
                    </v-combobox>

                    <h4>Idiomas que quieres practicar</h4>
                    <v-combobox
                            v-model="chips_languages_to_learn"
                            :items="items_languages_to_learn"
                            chips
                            clearable
                            label="¿Qué idiomas buscas mejorar?"
                            multiple
                            solo
                    >
                        <template v-slot:item="{ index, item }">
                            <v-chip
                                    color="white"
                                    block
                            >
                                <v-avatar left>
                                    <v-img :src="createPathToFlagImage(item.id)"></v-img>
                                </v-avatar>
                                <strong>{{ item.text }}</strong>&nbsp;
                            </v-chip>
                        </template>
                        <!--<template v-slot:item="{ index, item }">
                            <v-chip
                                    color="white"
                                    block
                            >
                                <v-avatar left>
                                    <v-img :src="createPathToFlagImage(item.id)"></v-img>
                                </v-avatar>
                                <strong>{{ item.text }}</strong>&nbsp;
                            </v-chip>
                        </template>-->
                        <template v-slot:selection="{ attrs, item, select, selected }">
                            <v-chip
                                    v-bind="attrs"
                                    :input-value="selected"
                                    close
                                    @click="select"
                                    @click:close="remove_language_to_learn(item)"
                            >
                                <v-avatar left>
                                    <v-img :src = "createPathToFlagImage(item.id)"></v-img>
                                </v-avatar>
                                <strong>{{ item.text }}</strong>&nbsp;
                            </v-chip>
                        </template>
                    </v-combobox>

                    <v-btn block color="primary" @click="setLanguages" :disabled="buttonIsDisabled">Continuar</v-btn>
                </v-container>
            </v-card>
        </v-col>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="blue"
                    text
                    @click="snackbar = false"
            >
                Cerrar
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    const languages = [
        {
            text: 'Alemán',
            id: 'de'
        },
        {
            text: 'Chino',
            id: 'zh'
        },{
            text: 'Español',
            id: 'es'
        },
        {
            text: 'Francés',
            id: 'fr'
        },
        {
            text: 'Inglés',
            id: 'en'
        },
        {
            text: 'Italiano',
            id: 'it'
        },
        {
            text: 'Japonés',
            id: 'ja'
        },
        {
            text: 'Portugués',
            id: 'pt'
        }
    ];

    import * as firebase from "firebase";

    export default {
        name: "FillYourLanguages",
        data () {
            return {
                buttonIsDisabled: true,
                snackbar: false,
                snackbarText:'',
                chips_known_languages: [],
                chips_languages_to_learn: [],
                items_known_languages: languages,
                items_languages_to_learn: languages,
            }
        },

        methods: {
            remove_known_language (item) {
                this.chips_known_languages.splice(this.chips_known_languages.indexOf(item), 1)
                this.chips_known_languages = [...this.chips_known_languages]
            },
            remove_language_to_learn (item) {
                this.chips_languages_to_learn.splice(this.chips_languages_to_learn.indexOf(item), 1)
                this.chips_languages_to_learn = [...this.chips_languages_to_learn]
            },
            validLanguages() {
                if (this.chips_known_languages.length > 0 && this.chips_languages_to_learn.length > 0) {
                    this.buttonIsDisabled = false;
                } else {
                    this.buttonIsDisabled = true;
                }
            },
            filter_items_known_languages: function(){
                // eslint-disable-next-line no-unused-vars
                let self = this;
                this.items_known_languages = languages.filter(function (value) {
                    return !self.chips_languages_to_learn.includes(value);
                })
            },
            filter_items_languages_to_learn: function(){
                // eslint-disable-next-line no-unused-vars
                let self = this;
                this.items_languages_to_learn = languages.filter(function (value) {
                    return !self.chips_known_languages.includes(value);
                })
            },
            setLanguages(){
                let firestoreDB = firebase.firestore();

                let self = this;

                firestoreDB.collection("UsersKnownLanguages").doc(firebase.auth().currentUser.uid).set(
                    {
                        known_languages: this.chips_known_languages,
                        languages_to_learn: this.chips_languages_to_learn
                    },
                    { merge: true }
                )
                    .then(() => {
                        console.log("Languages info successfully written in users");
                        this.$router.replace('set-your-profile-image');
                    })
                    .catch(function (error) {
                        console.error("Error writing languages info in users: ", error);
                        self.snackbarText = 'Parece que ha habido un problema de conexión. Por favor, vuelve a intentarlo más tarde.'
                        self.snackbar = true;
                        return
                    });

                firestoreDB.collection("Users").doc(firebase.auth().currentUser.uid).set(
                    {
                        known_languages: this.chips_known_languages.id,
                        languages_to_learn: this.chips_languages_to_learn.id
                    },
                    { merge: true }
                )
                    .then(() => {
                        console.log("Languages info successfully written in users");
                        this.$router.replace('set-your-profile-image');
                    })
                    .catch(function (error) {
                        console.error("Error writing languages info in users: ", error);
                        self.snackbarText = 'Parece que ha habido un problema de conexión. Por favor, vuelve a intentarlo más tarde.'
                        self.snackbar = true;
                        return
                    });
            },
            createPathToFlagImage: function (country) {
                return "img/flags/" + country + ".svg";
            }
        },
        watch: {
            chips_known_languages: function(){
                this.validLanguages();
                this.filter_items_languages_to_learn();
            } ,
            chips_languages_to_learn: function(){
                this.validLanguages();
                this.filter_items_known_languages();
            },

        }
    }
</script>

<style scoped>
    .languages_container{
        height: 40%;
    }

    .fill-your-languages{
        height: 100%;
        position: relative;
        width: 100%
    }
</style>