<template>
    <v-container class="send-change-password-email" style="position: relative">
        <v-row>
            <v-col cols="12">
                <h2>¿Has olvidado tu contraseña?</h2>
            </v-col>
        </v-row>

        <v-col cols="12">
            <v-card>
                <v-progress-linear
                        v-model="progressValue"
                        height="10"
                        color="blue-grey"
                        reactive
                        dark
                ></v-progress-linear>

                <v-container>
                    <p align="justify">
                        ¿Has olvidado tu contraseña? Introduce tu E-mail y te ayudaremos a recuperarla:
                    </p>

                    <v-form v-model="validForm">
                        <v-text-field
                                v-model="email"
                                :rules="[rules.required, rules.email]"
                                label="E-mail"
                                required
                        ></v-text-field>
                    </v-form>

                    <br>

                    <v-btn block color="primary" @click="sendChangePasswordEmail" :disabled="buttonIsDisabled">Restablecer contraseña</v-btn>
                </v-container>
            </v-card>
        </v-col>

        <v-dialog
                v-model="dialog"
        >
            <v-card>
                <br>
                <v-card-text align="justify" style="color: black; padding-bottom: 0px;">
                    <p style="margin-bottom: 7px">En un breve período de tiempo recibirás un email. Ábrelo y sigue las instrucciones para restablecer tu contraseña.</p>
                </v-card-text>

                <v-card-actions style="padding-bottom: 15px">
                    <v-spacer></v-spacer>
                    <v-btn @click="redirectToHome" color="primary">
                        Entendido
                    </v-btn>
                    <v-spacer></v-spacer>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="primary"
                    text
                    @click="snackbar = false"
            >
                Cerrar
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import * as firebase from "firebase";

    export default {
        name: "SendChangePasswordEmail",
        data() {
            return {
                progressValue: 100,
                validForm: false,
                email: '',
                buttonIsDisabled: true,
                dialog: false,
                snackbar: false,
                snackbarText: 'Parece que ha habido un problema de conexión. Por favor, vuelve a intentarlo más tarde.',
                rules: {
                    required: value => !!value || 'Este campo es obligatorio',
                    email: value => {
                        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
                        return pattern.test(value) || 'E-mail no válido'
                    }
                }
            }
        },
        methods: {
            sendChangePasswordEmail: function () {
                let auth = firebase.auth();
                let self = this;

                auth.sendPasswordResetEmail(this.email).then(function() {
                    // Email sent.
                    self.dialog = true;
                }).catch(function(error) {
                    // An error happened.
                    console.warn(error)
                    if(error.code != 'auth/user-not-found'){
                        self.snackbar = true;
                    }
                    else{
                        self.dialog = true;
                    }
                });

            },
            redirectToHome: function () {
                this.dialog = false;
                this.$router.replace('home');
            }
        },
        watch: {
            validForm: function (value) {
                if (value) {
                    this.buttonIsDisabled = false
                } else {
                    this.buttonIsDisabled = true
                }
            }
        }
    }
</script>

<style scoped>

</style>