import Vue from 'vue'
import Router from 'vue-router'
import firebase from "firebase";
import Home from './views/Home.vue'
import Login from "./views/Login";
import SignUp from "./views/SignUp";
import EmailVerification from "./views/EmailVerification";
import FillYourProfileInfo from "./views/FillYourProfileInfo";
import FillYourLanguages from "./views/FillYourLanguages";
import SetYourProfileImage from "./views/SetYourProfileImage";
import SendChangePasswordEmail from "./views/SendChangePasswordEmail";

Vue.use(Router)

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      //To redirect all routes that do not exist to /home
      path: '*',
      redirect: '/home'
    },
    {
      //To redirect root route to /home
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'home',
      component: Home
    },
    {
      path:'/login',
      name:'login',
      component: Login
    },
    {
      path:'/sign-up',
      name: 'signUp',
      component: SignUp
    },
    {
      path:'/email-verification',
      name: 'emailVerification',
      component: EmailVerification,
      meta: {
        requiresAuth: true
      }
    },{
      path:'/fill-your-profile-info',
      name: 'fillYourProfileInfo',
      component: FillYourProfileInfo,
      meta: {
        requiresAuth: true
      }
    },{
      path:'/fill-your-languages',
      name: 'fillYourLanguages',
      component: FillYourLanguages,
      meta: {
        requiresAuth: true
      }
    }, {
      path: '/set-your-profile-image',
      name: 'setYourProfileImage',
      component: SetYourProfileImage,
      meta: {
        requiresAuth: true
      }
    },{
      path:'/send-change-password-email',
      name: 'sendChangePasswordEmail',
      component: SendChangePasswordEmail,
      meta: {
        requiresAuth: false
      }
    },
    {
      path: '/dashboard',
      name: 'dashboard',
      redirect:{name:'profileHome'},
      meta: { requiresAuth: true },
      component: () => import('./views/Dashboard.vue'),
      children: [
        {
          path: 'ranking',
          name: 'rankingHome',
          meta: { requiresAuth: true, menu:'ranking'  },
          component: () => import('./views/Ranking/RankingHome.vue'),
        },
        {
          path: 'events',
          name: 'eventsHome',
          meta: { requiresAuth: true, menu:'events'  },
          component: () => import('./views/Events/EventsHome.vue'),
        },
        {
          path: 'search',
          name: 'searchHome',
          meta: { requiresAuth: true, menu:'search'  },
          component: () => import('./views/Search/SearchHome.vue'),
        },
        {
          path: 'chat',
          name: 'chatHome',
          meta: { requiresAuth: true, menu:'chat'  },
          component: () => import('./views/Chat/ChatHome.vue'),
        },
        {
          path: 'profile',
          name: 'profileHome',
          meta: { requiresAuth: true, menu:'profile'  },
          component: () => import('./views/Profile/ProfileHome.vue'),
        }
      ]
    }
  ]
});

router.beforeEach((to, from, next) => {
  const currentUser = firebase.auth().currentUser;
  const requiresAuth = to.matched.some(record => record.meta.requiresAuth);

  if(requiresAuth && !currentUser){
    next('home');
  } else if(to.matched.some(record =>['login','sign-up','home'].includes(record.name))){
    if (currentUser) {
      next({
        path: '/dashboard'
      })
    }
  }
  else{
    next();
  }
});

export default router;
