<template>
    <v-container class="login" style="position: relative">
        <v-row>
            <v-col cols="12">
                <h2>Accede a tu cuenta</h2>
            </v-col>
        </v-row>

        <v-col cols="12">
            <v-card>
                <v-progress-linear
                        value="100"
                        height="10"
                        color="blue-grey"
                        reactive
                        dark
                ></v-progress-linear>

                <v-container>
                    <v-form v-model="validForm">
                        <v-text-field
                                v-model="email"
                                :rules="[rules.required]"
                                label="E-mail"
                                required
                        ></v-text-field>

                        <v-text-field
                                v-model="password"
                                :rules="[rules.required]"
                                :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                                :type="showPassword ? 'text' : 'password'"
                                label="Contraseña"
                                @click:append="showPassword = !showPassword"
                        ></v-text-field>
                    </v-form>

                    <v-btn block color="primary" @click="login" :disabled="buttonIsDisabled">Iniciar sesión</v-btn>
                </v-container>
            </v-card>
        </v-col>

        <v-col cols="12" align="center">
            <p><router-link to="/send-change-password-email">¿Has olvidado tu contraseña?</router-link></p>
        </v-col>

        <v-col cols="12" align="center" id="sign-up-now-claim">
            <p>¿Todavía no tienes una cuenta? <router-link to="/sign-up">¡Regístrate ahora!</router-link></p>
        </v-col>
        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="blue"
                    text
                    @click="snackbar = false"
            >
                Cerrar
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import * as firebase from "firebase";

    export default {
        name: "login",
        data() {
            return {
                validForm: false,
                email: '',
                password: '',
                showPassword: false,
                buttonIsDisabled: true,
                snackbar: false,
                snackbarText: '',
                rules: {
                    required: value => !!value || 'Este campo es obligatorio'
                }
            }
        },
        methods: {
            login: function () {
                firebase.auth().signInWithEmailAndPassword(this.email, this.password).then((success) => {
                        //if correct log
                        firebase.auth().setPersistence(firebase.auth.Auth.Persistence.LOCAL)
                            .then(() => {
                                if (success.user.emailVerified) {
                                    //if email is verificated
                                    this.$router.replace('home');

                                } else {
                                    this.$router.replace('email-verification');

                                }
                            })
                            .catch((error) => {
                                console.warn(error)
                                this.snackbarText = 'Parece que ha habido un problema de conexión. Por favor, vuelve a intentarlo más tarde.'
                                this.snackbar = true
                            });
                    }
                )
                    .catch((error) => {
                        console.warn(error)
                        this.snackbarText = 'E-mail y/o contraseña incorrectos.'
                        this.snackbar = true
                    });
            },
        },
        watch: {
            validForm: function (value) {
                if (value) {
                    this.buttonIsDisabled = false
                } else {
                    this.buttonIsDisabled = true
                }
            }
        }
    }
</script>

<style scoped>
    #sign-up-now-claim{
        width: 92.5%;
        bottom:1%;
        left: 50%;
        transform: translate(-50%, 0);
        position: fixed;
    }
</style>
