<template>
    <v-container style="height: 100%; position: relative; width: 100%" align="center">
        <v-row>
            <v-col cols="12">
                <h2>Elige tu foto de perfil</h2>
            </v-col>
        </v-row>

        <v-row style="height: 100%">
            <v-col cols="12"
                   sm="6" offset-sm="3"
                   md="4" offset-md="4"
                   style="height: 100%">
                <v-card
                        class="mx-auto"
                        style="height: 75%"
                        align="center"
                >
                    <v-progress-linear
                            value="100"
                            height="10px"
                            color="blue-grey"
                            dark
                    >
                    </v-progress-linear>

                    <v-avatar class="thumbnail" style="width: 95%; padding-top: 95%; vertical-align: top; position: relative; margin-top: 5%">
                            <img
                            v-if="showImg"
                            style="width: 100%; position: absolute; top: 0; height: 100%; object-position: center; object-fit: cover;"
                            class="white--text align-end"
                            :aspect-ratio="1"
                            :src="imagePreview"
                            >
                    </v-avatar>

                    <v-btn
                            absolute
                            dark
                            fab
                            right
                            color="success"
                            @click="$refs.myFiles.click()"
                            class="button-open-camera"
                    >
                        <v-icon>mdi-camera</v-icon>
                    </v-btn>

                    <v-container class="bottom-centered-buttons">
                        <v-btn block color="primary" @click="uploadUserProfileImage" :disabled="buttonIsDisabled">Continuar</v-btn>
                    </v-container>
                </v-card>
            </v-col>
        </v-row>

        <input hidden ref="myFiles" @change="previewFiles" type="file" accept="image/*" capture>
        <!--SnackBar-->
        <v-snackbar
                v-model="snackbar"
                :timeout="2500"
                :top="true"
        >
            {{ textSnackbar }}
            <v-btn
                    color="pink"
                    flat
                    @click="snackbar = false"
            >
                Close
            </v-btn>
        </v-snackbar>
        <!--End SnackBar-->
    </v-container>

</template>

<script>
    import Vue from 'vue'
    import firebase from 'firebase'
    import imageCompression from 'browser-image-compression'

    let defaultUserImage = 'img/defaultUserImage.svg';

    export default {
        name: "SetYourProfileImage",
        data: () => ({
            showImg: true,
            imagePreview: defaultUserImage,
            buttonIsDisabled: false,
            imageUpload: defaultUserImage.replace("data:image/jpeg;base64,", "").replace("data:image/png;base64,", ""),
            snackbar: false,
            textSnackbar: 'Error'
        }),
        methods: {
            previewFiles() {
                if (this.$refs.myFiles.files.length > 0) {
                    if (!this.$refs.myFiles.files[0].type.includes('image/')) {
                        alert('Por favor, selecciona una imagen');
                        return
                    }

                    let imageFile = this.$refs.myFiles.files[0];

                    let options = {
                        maxSizeMB: 0.5,
                        maxWidthOrHeight: 500,
                        useWebWorker: true
                    };

                    imageCompression(imageFile, options)
                        .then((compressedFile) => {
                            if (typeof FileReader === 'function') {
                                const reader = new FileReader();
                                reader.onload = (event) => {
                                    this.imgSrc = event.target.result;
                                    this.imagePreview = this.imgSrc;
                                    this.imageUpload = this.imagePreview.replace("data:image/jpeg;base64,", "").replace("data:image/png;base64,", "");
                                    console.log(this.imagePreview);
                                    this.showImg = false;
                                    Vue.nextTick(() => {
                                        this.showImg = true;
                                    })
                                };
                                reader.readAsDataURL(compressedFile)
                            } else {
                                alert('Lo sentimos, la API FileReader no está soportada')
                            }
                        })
                        .catch(function (error) {
                            console.log(error.message);
                        });
                } else {
                    console.warn("img error")
                }
            },
            uploadUserProfileImage() {
                Vue.nextTick(() => {
                    let firestoreDB = firebase.firestore();
                    let userID = firebase.auth().currentUser.uid;
                    let storageDB = firebase.storage().ref().child("UserProfileImages/" + userID);
                    storageDB.putString(this.imageUpload, 'base64')
                        .then(() => {
                            console.log('User profile image uploaded');
                            storageDB.getDownloadURL().then((url) => {
                                firestoreDB.collection("Users").doc(userID).set(
                                    {
                                        profileImage: url
                                    },
                                    {merge: true}
                                )
                                    .then(() => {
                                        console.log("User profile image url successfully written in users");
                                        this.$router.replace('fill-your-languages');
                                    })
                                    .catch(function (error) {
                                        console.error("Error writing user profile image url info in users: ", error);
                                        this.snackbarText = 'Parece que ha habido un problema de conexión. Por favor, vuelve a intentarlo más tarde.'
                                        this.snackbar = true
                                        return
                                    });
                            })
                        })
                        .catch(() => {
                            this.snackbar = true
                            this.textSnackbar = 'Error subiendo la imagen'
                        })
                })
            },
        },
        beforeMount(){
            let firestoreDB = firebase.firestore();
            let userID = firebase.auth().currentUser.uid;

            firestoreDB.collection("Users").doc(userID).get().then(function(doc) {
                if (doc.exists) {
                    if(doc.data().gender == "Hombre"){
                        this.defaultUserImage = "img/male-defaultUserImage.svg";
                    }
                    else if(doc.data().gender == "Mujer"){
                        this.defaultUserImage = "img/female-defaultUserImage.svg";
                    }
                } else {
                    console.log("No such document!");
                }
            }).catch(function(error) {
                console.log("Error getting document:", error);
                this.snackbarText = 'Parece que ha habido un problema de conexión. Por favor, asegúrese de estar conectado a Internet.'
                this.snackbar = true
            });
        }
    }
</script>

<style scoped>
    .button-open-camera {
        margin-top: 85%;
    }

    .bottom-centered-buttons{
        width: 92.5%;
        bottom:3%;
        left: 50%;
        transform: translate(-50%, 0);
        position: absolute;
    }
</style>