<template>
    <v-container>
        <v-row>
            <v-col cols="12">
                <h2>Rellena tu perfil</h2>
            </v-col>
            <v-col cols="12">
                <v-card>
                    <v-progress-linear
                            value="25"
                            height="10"
                            color="blue-grey"
                            reactive
                            dark
                    >
                    </v-progress-linear>

                    <v-container>
                        <v-form v-model="validForm">
                            <v-text-field
                                    v-model="name"
                                    :rules="[rules.required]"
                                    label="Nombre"
                                    required
                            ></v-text-field>

                            <v-text-field
                                    v-model="lastName"
                                    :rules="[rules.required]"
                                    label="Apellidos"
                                    required
                            ></v-text-field>

                            <v-dialog
                                    ref="dialog"
                                    v-model="modal"
                                    :return-value.sync="birthDate"
                                    persistent
                            >
                                <template v-slot:activator="{ on }">
                                    <v-text-field
                                            v-model="birthDate"
                                            label="Fecha de nacimiento"
                                            hint="AAAA-MM-DD"
                                            append-icon="mdi-calendar"
                                            :rules="[rules.required, rules.minAge]"
                                            readonly
                                            v-on="on"
                                            @click="setDefaultBirthDate"
                                            required
                                    ></v-text-field>
                                </template>
                                <v-date-picker v-model="birthDate" scrollable width="97%" locale="es">
                                    <v-spacer></v-spacer>
                                    <v-btn text color="primary" @click="modal = false">Cancel</v-btn>
                                    <v-btn text color="primary" @click="$refs.dialog.save(birthDate)">OK</v-btn>
                                </v-date-picker>
                            </v-dialog>

                            <v-select
                                    :rules="[rules.required]"
                                    style="margin-top: 15px"
                                    :items="genderOptions"
                                    item-text="option"
                                    label="Género"
                                    dense
                                    return-object
                            >

                                <template v-slot:item="{item}">
                                    <v-avatar>
                                        <img :src="item.img" style="height: 60%; width: auto"/>
                                    </v-avatar>
                                    {{    item.option }}
                                </template>
                                <template v-slot:selection="{item, select, selected }">
                                    <v-avatar>
                                        <img :src="item.img" style="height: 50%; width: auto"/>
                                    </v-avatar>
                                    {{  item.option }}
                                </template>
                            </v-select>

                            <v-textarea
                                    style="margin-top: 15px"
                                    outlined
                                    v-model="description"
                                    :rules="[rules.required, rules.min20Char]"
                                    label="Cuéntanos un poco sobre ti"
                                    required
                                    counter
                                    maxlength="140"
                            ></v-textarea>
                        </v-form>

                        <v-btn block color="primary" @click="setProfileInfo" :disabled="buttonIsDisabled">Continuar</v-btn>
                    </v-container>
                </v-card>
            </v-col>

            <!-- Error snackbar -->
            <v-snackbar
                    v-model="snackbar"
                    :timeout="4000"
            >
                {{ snackbarText }}
                <v-btn
                        color="blue"
                        text
                        @click="snackbar = false"
                >
                    Cerrar
                </v-btn>
            </v-snackbar>
            <!--End snackbar-->
        </v-row>
    </v-container>
</template>

<script>
    import * as firebase from "firebase";
    const minimumAge = 16;
    
    export default {
        name: "fillYourProfileInfo",
        data() {
            return {
                buttonIsDisabled: true,
                snackbar: false,
                snackbarText: '',
                name: '',
                lastName: '',
                birthDate: null,
                firstTimeOpenCalendar: true,
                menu: false,
                modal: false,
                description: '',
                gender: null,
                validForm: false,
                genderOptions: [
                    {
                        option: "Hombre",
                        img: "img/genders/male.svg"
                    },
                    {
                        option: "Mujer",
                        img: "img/genders/female.svg"
                    }
                ],
                rules: {
                    required: value => !!value || 'Este campo es obligatorio',
                    min20Char: value => value.length >= 20 || 'Mínimo 20 caracteres',
                    minAge: value => {
                        const bDate = new Date(value);
                        const currentDate = new Date();
                        let validAge;
                        if (currentDate.getFullYear() - bDate.getFullYear() < minimumAge) {
                            validAge = false;
                        } else if (currentDate.getFullYear() - bDate.getFullYear() == minimumAge) {
                            if (currentDate.getMonth() < bDate.getMonth()) {
                                validAge = false;
                            } else if (currentDate.getMonth() == bDate.getMonth()) {
                                if (currentDate.getDate() < bDate.getDate()) {
                                    validAge = false;
                                } else {
                                    validAge = true;
                                }
                            } else {
                                validAge = true;
                            }
                        } else {
                            validAge = true;
                        }

                        return validAge || 'Debes tener al menos minimumAge años para poder registrarte';
                    },
                    samePassword: value => value.localeCompare(this.password) == 0 || 'Las contraseñas no coinciden',
                }
            }
        },
        methods: {
            setProfileInfo: function () {
                let firestoreDB = firebase.firestore();
                console.log(firebase.auth().currentUser.uid)
                firestoreDB.collection("Users").doc(firebase.auth().currentUser.uid).set(
                    {
                        name: this.name,
                        lastName: this.lastName,
                        birthDate: this.birthDate,
                        gender: this.gender.option,
                        description: this.description
                    },
                    {merge: true}
                )
                    .then(() => {
                        console.log("Profile info successfully written in users");
                        this.$router.replace('fill-your-languages');
                    })
                    .catch(function (error) {
                        console.error("Error writing profile info in users: ", error);
                        this.snackbarText = 'Parece que ha habido un problema de conexión. Por favor, vuelve a intentarlo más tarde.'
                        this.snackbar = true
                        return
                    });
            },
            setDefaultBirthDate: function(){
                if(this.firstTimeOpenCalendar){
                    let currentDate = new Date();
                    currentDate.setFullYear(currentDate.getFullYear()-minimumAge);
                    this.birthDate = currentDate.toISOString().substr(0, 10)
                    this.firstTimeOpenCalendar = false;
                }
            }
        },
        watch: {
            validForm: function (value) {
                if (value) {
                    this.buttonIsDisabled = false
                } else {
                    this.buttonIsDisabled = true
                }
            }
        }
    }
</script>

